.reviews-gallery {
  @media (min-width: $tablet) {
    align-self: center;
  }

  @media (min-width: $desktop) {
    align-self: flex-end;
    width: 100%;
  }
}

.reviews-gallery__photos-wrapper {
  position: relative;
  display: flex;
  justify-content: flex-end;
  margin: 129px 0 41px;

  @media (min-width: $tablet) {
    margin: 130px 0 41px;
  }

  @media (min-width: $desktop) {
    margin: 45px 32px 41px 40px;
  }
}

.reviews-gallery__photo {
  position: absolute;
  top: -93px;
  left: 50%;
  transform: translateX(-50%);

  @media (min-width: $tablet) {
    top: -95px;
    right: -153px;
    left: auto;
    transform: translateX(-96%);
  }

  @media (min-width: $desktop) {
    top: 46px;
    right: 162px;
  }
}

.reviews-gallery__image {
  width: 228px;
  height: 183px;
  object-fit: cover;

  @media (min-width: $tablet) {
    width: 303px;
    height: 208px;
  }

  @media (min-width: $desktop) {
    width: 481px;
    height: 331px;
  }
}

.reviews-gallery__photo-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 113px 0 34px;
  background-color: $shadow-black;
  column-gap: 36px;

  @media (min-width: $tablet) {
    margin: 0;
    max-width: 578px;
    padding: 146px 87px 27px 109px;
  }

  @media (min-width: $desktop) {
    justify-content: flex-start;
    max-width: 640px;
    margin: 0 84px 0 0;
    padding: 108px 90px 95px 145px;
    column-gap: 38px;
  }
}

.reviews-gallery__name {
  @include text-mobile;
  font-weight: 500;
  color: $gold;

  @media (min-width: $tablet) {
    @include gallery-text;
    flex-grow: 1;
    text-align: center;
  }

  @media (min-width: $desktop) {
    text-align: left;
  }
}

.reviews-gallery__position {
  color: $gold;

  @media (min-width: $tablet) {
    @include paragraph;
    line-height: 22px;
  }
}

.reviews-gallery__blockquote {
  @include section-paragraph-mobile;
  opacity: 0.72;
  text-align: center;
  color: $white;
  margin: 15px 25px 18px 25px;

  @media (min-width: $tablet) {
    flex-grow: 1;
    text-align: center;
    margin: 16px 15px 28px 15px;
    line-height: 22px;
  }

  @media (min-width: $desktop) {
    text-align: left;
    margin: 15px 0 39px;
  }
}

.reviews-gallery__link {
  @include button-link;
}

.reviews-gallery__sliders {
  position: absolute;
  display: flex;
  justify-content: space-between;
  top: -22px;
  left: 50%;
  transform: translateX(-50%);
  min-width: 305px;

  @media (min-width: $tablet) {
    top: 38px;
    left: 292px;
    min-width: 481px;
  }

  @media (min-width: $desktop) {
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    left: auto;
    bottom:auto;
    right: auto;
  }
}

.reviews-gallery__slider {
  background: none;
  border: none;
  outline: none;
  padding: 0;
  stroke: $gold;
  background-repeat: no-repeat;
  background-size: 100%;
  cursor: pointer;
}

.reviews-gallery__arrow {
  &--mobile {
    @media (min-width: $tablet) {
      display: none;
   }
  }

  &--tablet {
    display: none;

    @media (min-width: $tablet) {
      display: block;
      width: 59px;
      height: 16px;
   }
  }
}

.reviews-gallery__social-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  column-gap: 15px;
  order: 2;
}

.reviews-gallery__icon {
  border: 1px solid $gold;
  fill: $gold;
  cursor: pointer;
  transition: .3s;

  &:hover {
    transform: scale(1.3);
    border: 1px solid $white;
  }
}

.reviews-gallery__date {
  @include link-text-mobile;
  align-self: center;
  order: 3;
  line-height: 17px;
  color: $gold;
  text-align: center;
  order: 1;
}
